ul li {
  list-style: none;
}

ul {
  text-align: left;
}

li {
  cursor: pointer;
  padding: 15px 0;
  position: relative;
  color: #d1d1d1;
}

li:hover {
  color: #fff;
}
.isActive {
  color: #fff;
}
.isActive:after {
  content: "âœ”";
  position: absolute;
  color: #b9ecb9;
  right: 0;
  top: 40%;
  font-size: 30px;
  transform: translateY(-50%);
}

.App {
  font-family: sans-serif;
  text-align: center;
}

.select-bar {
  position: fixed;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  color: #fff;
  border-radius: 8px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
  cursor: default;
  right: 20px;
  top: 20px;
  width: 250px;
}

.info-wrapper {
  text-align: left;
}

.select-bar:hover {
  cursor: move;
}

.btn-wrapper {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.btn {
  border: none;
  background: rgba(255, 255, 255, 0.7);
  border-radius: 5px;
  font-size: 90%;
  cursor: pointer;
  padding: 2px 8px;
  margin: 10px 5px;
}

.select-bar_info {
  font-size: 0.9em;
}

.select-bar_info span {
  font-style: italic;
  text-decoration: underline;
}
